body {
  margin: 0;
  color: rgb(46, 46, 46);
}
.container {
  display: flex;
  height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  background-color: white;
  font-family: "Raleway", sans-serif;
  flex-direction: column;
}
h1 {
  font-size: 62px;
  text-transform: capitalize;
}
.wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 80%;
  height: 60vh;
}
.field {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);

  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 1px, rgba(0, 0, 0, 0.07) 0px 2px 2px,
    rgba(0, 0, 0, 0.07) 0px 4px 4px, rgba(0, 0, 0, 0.07) 0px 8px 8px,
    rgba(0, 0, 0, 0.07) 0px 16px 16px;
}
.field:disabled {
  background-color: grey;
  cursor: not-allowed;
}
.box {
  width: 100px;
  height: 100px;
  border: 1px solid rgb(223, 223, 223);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 60px;
}

.result {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.result_text {
  font-size: 32px;
}

.play_button {
  background-color: transparent;
  border: 3px solid #69a6d1;
  color: #69a6d1;
  width: 250px;
  height: 60px;
  text-transform: uppercase;
  font-size: 24px;
}
.play_button:hover {
  border: 3px solid #fcadb0;
  color: #fcadb0;
  cursor: pointer;
}
.play_button:active {
  border: 3px solid #ffd4b1;
  color: #ffd4b1;
}
